html, 
body {
  width: 100%;
  height: 100%;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
#root {
  width: 100%;
  height: 100%;
  background-color: rgb(237, 236, 235);
}

.App-header {
  text-align: center;
  font-weight: bold;
  font-size: 38px;
  font-family: 'Roboto', sans-serif;
}

.modal-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: rgb(51, 62, 75);
  border-radius: 5px;
  width: 500px;
  color: aliceblue;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.madal-box-buttonts {
  display: flex;
  width: 100%;
}
.madal-box-buttonts button {
  background-color: rgb(100, 118, 141);
  color: rgb(240, 248, 255);
  border: none;
  outline: none;
  cursor: pointer;

  width: 100%;
  padding: 10px 0;

  font-size: 18px;

  opacity: 0.5;

  transition: .3s;
}
.madal-box-buttonts button:hover {
  background-color: rgb(95, 158, 160);
  opacity: 0.8;
}
.madal-box-buttonts button.active {
  background-color: rgb(95, 158, 160);
  opacity: 1;
}

.tabcontent {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.component-title {
  text-align: center;
  font-weight: normal;
  font-size: 22px;
  font-family: 'Roboto', sans-serif;
  margin: 20px 0;
}

.form {
  display: flex;
  flex-direction: column;
  position: relative;
}

.form input {
  background-color: transparent;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.501);
  color: rgba(255, 255, 255, 0.508);
  font-weight: normal;

  font-size: 16px;
  padding: 10px;

  transition: .3s;
}
.form input:hover {
  border: 1px solid rgb(255, 255, 255);
  color: rgb(255, 255, 255);
}
.form input:focus {
  border: 1px solid rgb(255, 255, 255);
  color: rgb(255, 255, 255);
}

.form input::placeholder{
  color: inherit;
  opacity: 0.7;
}
.is-danger {
  border: 1px solid red !important;
}

.input-icon {
  position: relative;
}
.input-icon input{
  width: 100%;
}
.icon {
  position: absolute;
  right: 10px;
  top: 10px;
  opacity: 0.5;

  cursor: pointer;

  width: 20px;
  height: 20px;
}
.icon:hover {
  opacity: 1;
}

.form button {
  color: inherit;
  padding: 10px;
  background-color: rgb(95, 158, 160);
  outline: none;
  border: none;
  cursor: pointer;
  font-weight: 900;
  font-size: 18px;

  transition: .3s;
}
.form button:hover {
  -webkit-box-shadow: 0px 1px 20px 1px rgba(255, 255, 255, 0.2);
  -moz-box-shadow: 0px 1px 20px 1px rgba(255, 255, 255, 0.2);
  box-shadow: 0px 1px 20px 1px rgba(255, 255, 255, 0.2);
}

.form span {
  margin: 10px 0;
  text-align: right;
}
.name {
  display: flex;
  margin-bottom: 20px;
  justify-content: space-between;

  position: relative;
}
.form a {
  font-size: 12px;
  text-align: right;
  color: rgb(95, 158, 160);
  font-family: 'Roboto', sans-serif;
  text-decoration: none;

  font-weight: bold;
}
.form a:hover {
  opacity: 0.8;
}

@media (max-width: 525px) {
  .madal-box-buttonts {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .madal-box-buttonts button:first-child {
    margin-bottom: 20px;
  }

  .form p {
    display: flex;
    flex-direction: column;
  }
  .form p input:first-child {
    margin-bottom: 20px;
  }
  .modal-box {
    width: 90%;
  }
}

.alert-enter {
  opacity: 0;
}
.alert-enter.alert-enter-active {
  opacity: 1;
  transition: opacity 600ms linear;
}
.alert-exit {
  opacity: 0;
}
.alert-exit.alert-exit-active {
  opacity: 1;
  transition: opacity 600ms linear;
}
